<template>
  <div class="search">
    <b-row>
      <b-col>
        <b-card class="mb-1">
          <b-row>
            <b-col sm="2">
              <b-form-group class="mb-1">
                <b-form-input type="text" placeholder=""></b-form-input>
              </b-form-group>
            </b-col>
            <b-col sm="2">
              <b-form-group class="mb-1">
                <b-form-input type="text" placeholder=""></b-form-input>
              </b-form-group>
            </b-col>
            <b-col sm="2">
              <b-form-group class="mb-1">
                <b-form-input type="text" placeholder="IP"></b-form-input>
              </b-form-group>
            </b-col>
            <b-col sm="2">
              <b-form-group class="mb-1">
                <b-form-input type="text" placeholder=""></b-form-input>
              </b-form-group>
            </b-col>
            <b-col sm="2">
              <b-form-group class="mb-1">
                  <v-date-picker
                    mode='single'
                    v-model='selectedDate'>
                      <b-input
                        type='text'
                        icon='calendar'
                        :value='inputState.inputValue'
                        :placeholder='inputState.message'
                        @change.native='inputState.updateValue($event.target.value)'
                        expanded>
                      </b-input>
                  </v-date-picker>
              </b-form-group>
            </b-col>
            <b-col sm="2">
              <b-form-group class="mb-1">
                <b-form-select id="basicSelect"
                  :plain="true"
                  :options="['==','Option 1', 'Option 2', 'Option 3']"
                  value="==">
                </b-form-select>
              </b-form-group>
            </b-col>
            <b-col sm="2">
              <b-form-group class="mb-1">
                <b-form-select
                  :plain="true"
                  :options="['==','Option 1', 'Option 2', 'Option 3']" value="==">
                </b-form-select>
              </b-form-group>
            </b-col>
            <b-col sm="1">
            <b-form-group class="mb-1">
              <b-button block variant="primary" size="sm"><i class="fa fa-search"></i> </b-button>
            </b-form-group>
            </b-col>
            <b-col sm="1">
            <b-form-group class="mb-1">
              <b-button block variant="primary" size="sm"><i class="fa fa-plus-circle"></i> </b-button>
            </b-form-group>
            </b-col>
          </b-row>
        </b-card>
      </b-col>
    </b-row>
  </div>
</template>
<script>
import {setupCalendar,DatePicker } from 'v-calendar'
import 'v-calendar/lib/v-calendar.min.css'

setupCalendar({
  firstDayOfWeek: 2 // Monday
})

export default {
  name: 'search',
  components: {
    'VDatePicker': DatePicker,
  },
  data: () => {
    return {
       selectedDate: new Date(2018, 10, 10),
       timer: '2019-01-2',
    }
  },
  computed: {
    inputState() {
      return {
        type: 'is-primary',
        message: '',
      };
    },
  },
}
</script>